import React, { useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import { useQuery } from "react-query";
import { GetCategoryById } from "./actions/Actions";
import BCard from "../../components/layouts/BCard";
import { BSkeleton } from "../../components/layouts/BSkeleton";

const ShowCategory = () => {
  const { id } = useParams();

  const { data: category, isLoading } = useQuery(
    ["category", id],
    () => GetCategoryById(id),
    {
      staleTime: 0, // Enable caching for category data
    }
  );

  useEffect(() => {
    const fetchData = async () => {
      await GetCategoryById(id);
    };
    fetchData();
  }, [id]);

  return (
    <BCard cardTitle={category ? category.name : <BSkeleton />}>
      {isLoading ? (
        <BSkeleton />
      ) : category ? (
        <>
          <p>
            <span className="h6 fw-bold">Created At:</span>{" "}
            {category.created_at}
          </p>
          <p>
            <span className="h6 fw-bold">Updated At:</span>{" "}
            {category.updated_at}
          </p>
          <div className="py-1">
            <Link to="/categories" className="btn btn-danger shadow-none">
              Back
            </Link>
          </div>
        </>
      ) : (
        <BSkeleton />
      )}
    </BCard>
  );
};

export default ShowCategory;
