import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useParams, useNavigate } from "react-router-dom";
import BCard from "../../components/layouts/BCard";
import BInputField from "../../components/layouts/BInputField";
import BInputLabel from "../../components/layouts/BInputLabel";
import BButton from "../../components/layouts/BButton";
import { useQuery } from "react-query";
import { Update, GetUserById } from "../users/actions/Actions";
import { BSkeleton } from "../../components/layouts/BSkeleton";

const EditUser = () => {
  const {
    register,
    handleSubmit,
    setError,
    formState: { errors },
    setValue,
  } = useForm();

  const { id } = useParams();
  const navigate = useNavigate();

  const { data: userData, isLoading } = useQuery(["user", id], () => GetUserById(id));

  const onSubmit = (data) => {
    Update(id, data, setError, navigate);
  };

  useEffect(() => {
    if (userData) {
      setValue("uuid", userData?.uuid || "");
      setValue("email", userData?.email || "");
      setValue("username", userData?.username || "");
      setValue("password", userData?.password || "");
      setValue("access_token", userData?.access_token || "");
      setValue("created_at", userData.created_at || "");
      setValue(
        "updated_at",
        new Date().toDateString() + " At " + new Date().toLocaleTimeString()
      );
    }
  }, [userData, setValue]);

  return (
    <BCard cardTitle="Edit User">
      {isLoading ? (
        <BSkeleton />
      ) : (
        <form onSubmit={handleSubmit(onSubmit)}>
          {/* USERNAME */}
          <BInputLabel htmlFor="username" label="Username" />
          <BInputField
            type="text"
            id="username"
            name="username"
            placeholder="Username"
            register={register("username", {
              required: "Username is required.",
            })}
            error={errors.username}
          />

          {/* EMAIL */}
          <BInputLabel htmlFor="email" label="Email" />
          <BInputField
            type="email"
            id="email"
            name="email"
            placeholder="Email"
            register={register("email", {
              required: "Email is required.",
            })}
            error={errors.email}
          />

          <div className="py-3">
            <BButton type="submit" buttonText="Update" />
            <Link to="/users" className="btn btn-danger shadow-none">
              Cancel
            </Link>
          </div>
        </form>
      )}
    </BCard>
  );
};

export default EditUser;
