import React from "react";
import BModal from "../../components/layouts/BModal";
import { Destroy, GetAllCategories } from "./actions/Actions";
import { useQueryClient } from "react-query";

const DeleteCategory = ({ selectedCategory, closeDeleteModal }) => {
  const queryClient = useQueryClient();
  
  const fetchData = async () => {
    try {
      await GetAllCategories();
      queryClient.invalidateQueries("categories");
    } catch (error) {
      console.error(error);
    }
  };

  const Delete = async () => {
    const id = selectedCategory.id;
    try {
      await Destroy(id);
      fetchData();
      closeDeleteModal();
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <BModal
      title="Confirm Deletion"
      message={`Are you sure you want to delete this category? All the products of this category will be deleted as well.`}
      confirmText="Delete"
      cancelText="Cancel"
      onConfirm={Delete}
      onCancel={closeDeleteModal}
    />
  );
};

export default DeleteCategory;
