import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import BCard from "../../components/layouts/BCard";
import BInputField from "../../components/layouts/BInputField";
import BInputLabel from "../../components/layouts/BInputLabel";
import BButton from "../../components/layouts/BButton";
import { GetAllCategories } from "../categories/actions/Actions";
import { Store } from "./actions/Actions";

const CreateProduct = () => {
  const {
    register,
    handleSubmit,
    setError,
    formState: { errors },
  } = useForm();

  const navigate = useNavigate();
  const [categories, setCategories] = useState([]);


  const fetchCategories = async () => {
    try {
      const categories = await GetAllCategories(); // Use the GetAll function from Actions.js
      setCategories(categories);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  const onSubmit = (data) => {
    Store(data, setError, navigate);
  };


  return (
    <BCard cardTitle="Create Product">
      <form onSubmit={handleSubmit(onSubmit)}>
        {/* PRODUCT NAME */}
        <BInputLabel htmlFor="name" label="Product Name" />
        <BInputField
          type="text"
          id="name"
          name="name"
          placeholder="Product Name"
          register={register("name", {
            required: "Product name is required.",
          })}
          error={errors.name}
        />

        {/* PRODUCT DESCRIPTION */}
        <BInputLabel htmlFor="description" label="Product Description" />
        <BInputField
          type="text"
          id="description"
          name="description"
          placeholder="Product Description"
          defaultValue="N/A"
          register={register("description", {
            required: "Product description is required.",
          })}
          error={errors.description}
        />

        {/* PRODUCT CATEGORY */}
        <div className="py-1">
          <label className="fw-bold pb-1">Product Category</label>
          <select
            id="category"
            className="form-control"
            name="category"
            {...register("category", {
              required: "Product category is required.",
            })}>
            <option value="" className="text-muted">
              -- Select Category --
            </option>
            {categories.map((category) => (
              <option key={category.id} value={category.id}>
                {category.name}
              </option>
            ))}
          </select>
          {errors.category && (
            <small className="text-danger fw-bold">
              {errors.category.message}
            </small>
          )}
        </div>

        {/* PRODUCT PRICE */}
        <BInputLabel htmlFor="price" label="Product Price" />
        <BInputField
          type="text"
          id="price"
          name="price"
          placeholder="Product Price"
          register={register("price", {
            required: "Product price is required.",
          })}
          error={errors.price}
        />

        <div className="py-3">
          <BButton type="submit" buttonText="Add" />
          <Link to="/products" className="btn btn-danger shadow-none">
            Cancel
          </Link>
        </div>
      </form>
    </BCard>
  );
};

export default CreateProduct;
