// CreateCategory.js
import { useForm } from "react-hook-form";
import { Link, useNavigate } from "react-router-dom";
import BCard from "../../components/layouts/BCard";
import BInputField from "../../components/layouts/BInputField";
import BInputLabel from "../../components/layouts/BInputLabel";
import BButton from "../../components/layouts/BButton";
import { Store } from "./actions/Actions";

const CreateCategory = () => {
  const {
    register,
    handleSubmit,
    setError,
    formState: { errors },
  } = useForm();

  const navigate = useNavigate();

  const onSubmit = (data) => {
    Store(data, setError, navigate);
  };

  return (
    <BCard cardTitle="Create Category">
      <form onSubmit={handleSubmit(onSubmit)}>
        {/* CATEGORY NAME */}
        <BInputLabel htmlFor="name" label="Category Name" />
        <BInputField
          type="text"
          id="name"
          name="name"
          placeholder="Category Name"
          register={register("name", {
            required: "Category name is required.",
          })}
          error={errors.name}
        />
        <div className="py-3">
          <BButton type="submit" buttonText="Add" />
          <Link to="/categories" className="btn btn-danger shadow-none">
            Cancel
          </Link>
        </div>
      </form>
    </BCard>
  );
};

export default CreateCategory;
