import React, { useEffect, useState } from "react";
import BTable from "../../components/layouts/BTable";
import DeleteProduct from "./DeleteProduct";
import { useQuery } from "react-query";
import { GetAllProducts } from "./actions/Actions";

const GetProducts = () => {
  const { data: productsData } = useQuery("products", GetAllProducts);

  const [selectedProduct, setSelectedProduct] = useState(null);
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    if (productsData) {
      setCategories(productsData.categories);
    }
  }, [productsData]);

  const handleDelete = (product) => {
    setSelectedProduct(product);
  };

  const closeDeleteModal = () => {
    setSelectedProduct(null);
  };

  // Function to get the category name by ID
  const getCategoryName = (categoryId) => {
    const category = categories.find((category) => category.id === categoryId);
    return category ? category.name : "";
  };

  // Mapping the existingProducts array and replacing category ID with category name
  const mappedProducts = productsData
    ? productsData.products.map((product) => ({
        ...product,
        category: getCategoryName(product.category),
      }))
    : [];

  return (
    <>
        <BTable
          tableName="Products"
          buttonAddTitle="Add Product"
          data={mappedProducts}
          columnOrder={["name", "description", "price", "category"]}
          handleDelete={handleDelete}
          handleRoute="products"
        />

      {/* DELETE MODAL */}
      {selectedProduct && (
        <DeleteProduct
          selectedProduct={selectedProduct}
          closeDeleteModal={closeDeleteModal}
        />
      )}
    </>
  );
};

export default GetProducts;
