import React, { useEffect } from "react";
import { Route, Routes, Navigate, useNavigate } from "react-router-dom";
import { PrivateRoute } from "./PrivateRouter";
import Home from "./pages/Home";
import About from "./pages/About";
import Contact from "./pages/Contact";
import NotFound from "./pages/NotFound";

import Dashboard from "./pages/dashboard/Dashboard";
import QrCode from "./pages/dashboard/QrCode";
import Pages from "./pages/dashboard/Pages";

import Login from "./features/auth/Login";
import Users from "./pages/dashboard/Users";
import CreateUser from "./features/users/CreateUser";
import EditUser from "./features/users/EditUser";
import ShowUser from "./features/users/ShowUser";

import Categories from "./pages/dashboard/Categories";
import CreateCategory from "./features/categories/CreateCategory";
import EditCategory from "./features/categories/EditCategory";
import ShowCetgory from "./features/categories/ShowCategory";

import Products from "./pages/dashboard/Products";
import CreateProduct from "./features/products/CreateProduct";
import EditProduct from "./features/products/EditProduct";
import ShowProduct from "./features/products/ShowProduct";

const Router = () => {
  const isAuthenticated = sessionStorage.getItem("accessToken");
  const navigate = useNavigate();

  useEffect(() => {
    if (isAuthenticated && window.location.pathname === "/login") {
      navigate("/", { replace: true });
    }
  }, [isAuthenticated, navigate]);

  return (
    <Routes basename="/">
      <Route path="/" element={<Home />} />
      <Route path="/about" element={<About />} />
      <Route path="/contact" element={<Contact />} />
      <Route path="/*" element={<NotFound />} replace />
      <Route
        path="/login"
        element={isAuthenticated ? <Navigate to="/" replace /> : <Login />}
      />
      <Route
        path="/qrcode/*"
        element={
          <PrivateRoute>
            <QrCode />
          </PrivateRoute>
        }
      />
      <Route
        path="/pages/*"
        element={
          <PrivateRoute>
            <Pages />
          </PrivateRoute>
        }
      />
      <Route
        path="/dashboard/*"
        element={
          <PrivateRoute>
            <Dashboard />
          </PrivateRoute>
        }
      />
      <Route
        path="/users/*"
        element={
          <PrivateRoute>
            <Users />
          </PrivateRoute>
        }
      />
      <Route
        path="/users/:id/show"
        element={
          <PrivateRoute>
            <ShowUser />
          </PrivateRoute>
        }
      />
      <Route
        path="/users/create"
        element={
          <PrivateRoute>
            <CreateUser />
          </PrivateRoute>
        }
      />
      <Route
        path="/users/:id/edit"
        element={
          <PrivateRoute>
            <EditUser />
          </PrivateRoute>
        }
      />
      <Route
        path="/categories/*"
        element={
          <PrivateRoute>
            <Categories />
          </PrivateRoute>
        }
      />
      <Route
        path="/categories/create"
        element={
          <PrivateRoute>
            <CreateCategory />
          </PrivateRoute>
        }
      />
      <Route
        path="/categories/:id/edit"
        element={
          <PrivateRoute>
            <EditCategory />
          </PrivateRoute>
        }
      />
      <Route
        path="/categories/:id/show"
        element={
          <PrivateRoute>
            <ShowCetgory />
          </PrivateRoute>
        }
      />
      <Route
        path="/products/*"
        element={
          <PrivateRoute>
            <Products />
          </PrivateRoute>
        }
      />
      <Route
        path="/products/create"
        element={
          <PrivateRoute>
            <CreateProduct />
          </PrivateRoute>
        }
      />
      <Route
        path="/products/:id/edit"
        element={
          <PrivateRoute>
            <EditProduct />
          </PrivateRoute>
        }
      />
      <Route
        path="/products/:id/show"
        element={
          <PrivateRoute>
            <ShowProduct />
          </PrivateRoute>
        }
      />{" "}
    </Routes>
  );
};

export default Router;
