import { v4 as uuidv4 } from "uuid";
import { toast } from "react-toastify";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL;

export const Store = async (data, setError, navigate) => {
  const { username, email } = data;
  let { password } = data;

  // Set default password if not provided
  if (!password) {
    password = "PassWord2468!@#$%^&*()";
  }

  const url = `${API_URL}/users.json`;
  const user = {
    uuid: uuidv4(),
    created_at:
      new Date().toDateString() + " At " + new Date().toLocaleTimeString(),
    updated_at:
      new Date().toDateString() + " At " + new Date().toLocaleTimeString(),
    ...data,
    password,
  };
  const lowercaseUsername = username.toLowerCase();
  const lowercaseEmail = email.toLowerCase();

  try {
    const response = await axios.get(url);
    const existingUsers = response.data || {};

    if (
      Object.values(existingUsers).some(
        (user) => user.username.toLowerCase() === lowercaseUsername
      )
    ) {
      setError("username", {
        type: "manual",
        message: "Username is already taken.",
      });
      return;
    }

    if (
      Object.values(existingUsers).some(
        (user) => user.email.toLowerCase() === lowercaseEmail
      )
    ) {
      setError("email", {
        type: "manual",
        message: "Email is already taken.",
      });
      return;
    }

    // Password validation rules
    const hasLowerCase = /[a-z]/.test(password);
    const hasUpperCase = /[A-Z]/.test(password);
    const hasDigit = /\d/.test(password);
    const hasSpecialChar = /[!@#$%^&*]/.test(password);

    if (
      !hasLowerCase ||
      !hasUpperCase ||
      !hasDigit ||
      !hasSpecialChar ||
      password.length < 8
    ) {
      setError("password", {
        type: "manual",
        message:
          "Password must contain at least 8 characters, including at least one lowercase letter, one uppercase letter, one digit, and one special character.",
      });
      return;
    }

    // Generate an access token
    const accessToken = generateAccessToken();

    // Add the access token to the user object
    user.access_token = accessToken;

    const addUserResponse = await axios.post(url, user, {
      headers: {
        "Content-Type": "application/json",
      },
    });

    if (addUserResponse.status !== 200) {
      throw new Error("Error creating user.");
    }

    navigate("/users");
    toast.success("User Created Successfully.");
  } catch (error) {
    throw new Error("Error creating user.");
  }
};

export const Update = async (id, data, setError, navigate) => {
  const {
    uuid,
    username,
    email,
    password,
    access_token,
    created_at,
    updated_at,
  } = data;
  const updatedUser = {
    uuid,
    username,
    email,
    password,
    access_token,
    created_at,
    updated_at,
  };
  const url = `${API_URL}/users.json`;
  const urlId = `${API_URL}/users/${id}.json`;
  const lowercaseUsername = username.toLowerCase();
  const lowercaseEmail = email.toLowerCase();

  try {
    const accessToken = sessionStorage.getItem("accessToken");
    const authUsername = await getUserAuthName(accessToken);

    // Only allow admin and superadmin to edit users
    if (authUsername !== "admin" && authUsername !== "superadmin") {
      toast.error("UNAUTHORIZED ACCESS.");
      navigate("/users");
    } else {
      const response = await axios.get(url);
      const existingUsers = response.data || {};

      const isUsernameTaken = Object.values(existingUsers)
        .filter((user) => user.uuid !== uuid)
        .some((user) => user.username.toLowerCase() === lowercaseUsername);
      if (isUsernameTaken) {
        setError("username", {
          type: "manual",
          message: "Username is already taken.",
        });
        return;
      }

      const isEmailTaken = Object.values(existingUsers)
        .filter((user) => user.uuid !== uuid)
        .some((user) => user.email.toLowerCase() === lowercaseEmail);
      if (isEmailTaken) {
        setError("email", {
          type: "manual",
          message: "Email is already taken.",
        });
        return;
      }

      const updateUserResponse = await axios.put(urlId, updatedUser, {
        headers: {
          "Content-Type": "application/json",
        },
      });

      if (updateUserResponse.status !== 200) {
        throw new Error("Error updating user.");
      }

      toast.success("User Updated Successfully.");
      navigate("/users");
    }
  } catch (error) {
    console.error(error);
  }
};

export const Destroy = async (id, selectedUser) => {
  try {
    const accessToken = sessionStorage.getItem("accessToken");
    const authUsername = await getUserAuthName(accessToken);

    if (selectedUser.access_token === accessToken) {
      toast.error("THIS ACTION IS UNAUTHORIZED");
    } else if (authUsername === "admin" || authUsername === "superadmin") {
      if (selectedUser.username === "superadmin") {
        toast.error("PERMISSION DENIED.");
      } else {
        // Allow admin and superadmin to delete users
        const deletePromise = axios.delete(`${API_URL}/users/${id}.json`);
        toast.success("User Deleted Successfully.");
        await deletePromise;
      }
    } else {
      toast.error("PERMISSION DENIED");
    }
  } catch (error) {
    console.error(error);
  }
};

export const GetAllUsers = async () => {
  try {
    const url = `${API_URL}/users.json`;
    const response = await axios.get(url);

    const data = response.data || {};
    const users = [];
    for (const [id, user] of Object.entries(data)) {
      users.push({ id, ...user });
    }
    return users;
  } catch (error) {
    console.error(error);
    return [];
  }
};

export const GetUserById = async (id) => {
  try {
    const url = `${API_URL}/users/${id}.json`;
    const response = await axios.get(url);

    if (!response.status === 200) {
      throw new Error("Error Fetching Product.");
    }

    return response.data || {};
  } catch (error) {
    console.error(error);
    return null;
  }
};

export const CountUsers = async () => {
  const url = `${API_URL}/users.json`;

  try {
    const response = await axios.get(url);
    const data = response.data;
    const count = Object.keys(data || {}).length;

    return count;
  } catch (error) {
    console.error(error);
    return 0;
  }
};

// GENERATE ACCESS TOKEN
const generateAccessToken = () => {
  const characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  const tokenLength = 32;
  let accessToken = "";

  for (let i = 0; i < tokenLength; i++) {
    const randomIndex = Math.floor(Math.random() * characters.length);
    accessToken += characters.charAt(randomIndex);
  }

  return accessToken;
};

export const getUserAuthName = async (accessToken) => {
  try {
    const url = `${API_URL}/users.json`;
    const response = await axios.get(url);

    const data = response.data || {};
    const users = Object.values(data);

    const user = users.find((user) => user.access_token === accessToken);
    if (user) {
      return user.username;
    } else {
      throw new Error("User not found.");
    }
  } catch (error) {
    console.error(error);
    return null;
  }
};

export const isAuthenticated = async () => {
  try {
    const accessToken = sessionStorage.getItem("accessToken");
    if (!accessToken) {
      return false;
    }

    const url = `${API_URL}/users.json`;
    const response = await axios.get(url);

    const data = response.data || {};
    const users = Object.values(data);

    const user = users.find((user) => user.access_token === accessToken);
    return !!user; // Return true if user is found, false otherwise
  } catch (error) {
    console.error(error);
    return false;
  }
};
