import React, { useState } from "react";
import PropTypes from "prop-types";
import { NavLink as Link } from "react-router-dom";
import { BSkeleton } from "./BSkeleton";

const BTable = ({
  tableName,
  buttonAddTitle,
  data,
  columnOrder,
  handleRoute,
  handleDelete,
}) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [sortColumn, setSortColumn] = useState("");
  const [sortOrder, setSortOrder] = useState("asc");
  const pageSize = 10;
  const maxDisplayedPages = 5; // Number of pages to display in the pagination
  const [searchColumn, setSearchColumn] = useState(""); // New state for selected search column

  const filteredData = data.filter((item) =>
    Object.entries(item).some(([key, value]) => {
      if (key === "uuid" || key === "id" || key === "created_at" || key === "updated_at") {
        return false; // Exclude uuid and id properties from search
      }
      if (searchColumn) {
        // If a search column is selected, only search in that specific column
        return (
          key === searchColumn &&
          value.toString().toLowerCase().includes(searchTerm.toLowerCase())
        );
      }
      return value.toString().toLowerCase().includes(searchTerm.toLowerCase());
    })
  );

  const totalPages = Math.ceil(filteredData.length / pageSize);

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value);
    setCurrentPage(1);
  };

  const handleSearchColumnChange = (e) => {
    setSearchColumn(e.target.value);
    setSearchTerm(""); // Clear the search term when changing the search column
    setCurrentPage(1);
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const handlePreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const handleNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  const handleSort = (column) => {
    if (column === sortColumn) {
      // If the same column is clicked again, reverse the sort order
      setSortOrder(sortOrder === "asc" ? "desc" : "asc");
    } else {
      // If a new column is clicked, set it as the sort column and default to ascending order
      setSortColumn(column);
      setSortOrder("asc");
    }
  };

  const renderSortIcon = (column) => {
    if (column === sortColumn) {
      // Display the sort icon based on the current sort order
      return sortOrder === "asc" ? (
        <i className="fa fa-sort-up" aria-hidden="true"></i>
      ) : (
        <i className="fa fa-sort-down" aria-hidden="true"></i>
      );
    }
    // Display a neutral sort icon for columns not currently being sorted
    return <i className="fa fa-sort" aria-hidden="true"></i>;
  };

  const sortedData = [...filteredData].sort((a, b) => {
    if (sortColumn !== "") {
      const aValue = a[sortColumn];
      const bValue = b[sortColumn];

      // Handle numeric and string comparisons separately
      if (typeof aValue === "number" && typeof bValue === "number") {
        return sortOrder === "asc" ? aValue - bValue : bValue - aValue;
      } else {
        const aString = aValue.toString().toLowerCase();
        const bString = bValue.toString().toLowerCase();
        return sortOrder === "asc"
          ? aString.localeCompare(bString)
          : bString.localeCompare(aString);
      }
    }
    return 0; // No sort column specified, maintain original order
  });

  const renderPagination = () => {
    if (totalPages <= maxDisplayedPages) {
      // Show all pages if total pages are less than or equal to the maxDisplayedPages
      return Array.from({ length: totalPages }, (_, index) => (
        <li
          key={index}
          className={`page-item ${currentPage === index + 1 ? "active" : ""}`}>
          <button
            className="page-link shadow-none"
            onClick={() => handlePageChange(index + 1)}>
            {index + 1}
          </button>
        </li>
      ));
    } else {
      // Show first page, last page, and current page with ellipsis in between
      const pages = [];

      pages.push(
        <li
          key={1}
          className={`page-item ${currentPage === 1 ? "active" : ""}`}>
          <button
            className="page-link shadow-none"
            onClick={() => handlePageChange(1)}>
            1
          </button>
        </li>
      );

      // Calculate the start and end pages based on the current page and maxDisplayedPages
      const startPage = Math.max(
        2,
        currentPage - Math.floor(maxDisplayedPages / 2)
      );
      const endPage = Math.min(
        totalPages - 1,
        startPage + maxDisplayedPages - 3
      );

      if (startPage > 2) {
        pages.push(
          <li key="ellipsis-start" className="page-item disabled">
            <span className="page-link">...</span>
          </li>
        );
      }

      for (let page = startPage; page <= endPage; page++) {
        pages.push(
          <li
            key={page}
            className={`page-item ${currentPage === page ? "active" : ""}`}>
            <button
              className="page-link shadow-none"
              onClick={() => handlePageChange(page)}>
              {page}
            </button>
          </li>
        );
      }

      if (endPage < totalPages - 1) {
        pages.push(
          <li key="ellipsis-end" className="page-item disabled">
            <span className="page-link">...</span>
          </li>
        );
      }

      pages.push(
        <li
          key={totalPages}
          className={`page-item ${currentPage === totalPages ? "active" : ""}`}>
          <button
            className="page-link shadow-none"
            onClick={() => handlePageChange(totalPages)}>
            {totalPages}
          </button>
        </li>
      );

      return pages;
    }
  };

  const startIndex = (currentPage - 1) * pageSize;
  const endIndex = startIndex + pageSize;
  const paginatedData = sortedData.slice(startIndex, endIndex);


  const renderTableBody = () => {
    if (data.length === 0) {
      return (
        <tr>
          <td colSpan={columnOrder.length + 1}>
            <BSkeleton count={2}/>
          </td>
        </tr>
      );
    }

    if (paginatedData.length === 0 && searchTerm !== "") {
      return (
        <tr>
          <td colSpan={columnOrder.length + 1} className="text-center fw-bold">
            No records found.
          </td>
        </tr>
      );
    }

    return paginatedData.map((item) => {
      return (
        <tr key={item.id}>
          {columnOrder.map((column) => (
            <td
              key={column}
              className="text-truncate"
              style={{ maxWidth: "225px" }}>
              {item[column]}
            </td>
          ))}
          <td width="250px">
            <>
              <Link to={`/${handleRoute}/${item.id}/show`} className="">
                <i className="fa fa-eye btn btn-tranquilo" aria-hidden="true"></i>
              </Link>
              <Link
                to={`/${handleRoute}/${item.id}/edit`}
                className="m-2">
                <i
                  className="fa fa-pencil btn btn-second"
                  aria-hidden="true"></i>
              </Link>
              <span className="shadow-none" onClick={() => handleDelete(item)}>
                <i
                  className="fa fa-trash btn btn-danger"
                  aria-hidden="true"></i>
              </span>
            </>
          </td>
        </tr>
      );
    });
  };

  return (
    <>
      <div className="container py-5">
        <div className="row py-2">
          <div className="col-12 py-2">
            <h2 className="fw-bold">{tableName}</h2>
          </div>
          <div className="col-lg-7 col-md-4 col-sm-12 text-start pt-1 mb-3">
            <Link to={`/${handleRoute}/create`} className="btn btn-tranquilo">
              <i className="fa fa-plus" aria-hidden="true"></i> {buttonAddTitle}
            </Link>
          </div>

          <div className="col-lg-2 col-md-4 col-sm-12 text-end pt-1 mb-3">
            <select
              className="form-select shadow-none text-capitalize"
              value={searchColumn}
              onChange={handleSearchColumnChange}>
              <option value="" className="text-muted">
                Search By
              </option>
              {columnOrder.map((column) => (
                <option key={column} value={column} className="text-capitalize">
                  {column}
                </option>
              ))}
            </select>
          </div>

          <div className="col-lg-3 col-md-4 col-sm-12 text-end pt-1 mb-3">
            <input
              type="text"
              className="form-control"
              placeholder="Search..."
              value={searchTerm}
              onChange={handleSearchChange}
            />
          </div>
        </div>

        <div className="table-responsive">
          <table className="table table-bordered table-hover table-striped">
            <thead>
              <tr>
                {columnOrder.map((column) => (
                  <th key={column} onClick={() => handleSort(column)}>
                    {column} {renderSortIcon(column)}
                  </th>
                ))}
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>{renderTableBody()}</tbody>
          </table>
        </div>

        <div className="row">
          <div className="col-md-6 col-sm-12">
            <nav>
              <ul className="pagination shadow-none">
                {totalPages > 1 && (
                  <>
                    <li
                      className={`page-item ${
                        currentPage === 1 ? "disabled" : ""
                      }`}>
                      <button
                        className="page-link shadow-none"
                        onClick={handlePreviousPage}>
                        <i
                          className="fa fa-chevron-left"
                          aria-hidden="true"></i>
                      </button>
                    </li>
                    {renderPagination()}
                    <li
                      className={`page-item ${
                        currentPage === totalPages ? "disabled" : ""
                      }`}>
                      <button
                        className="page-link shadow-none"
                        onClick={handleNextPage}>
                        <i
                          className="fa fa-chevron-right"
                          aria-hidden="true"></i>
                      </button>
                    </li>
                  </>
                )}
              </ul>
            </nav>
          </div>
          <div className="col-md-6 col-sm-12 text-end pt-2">
            <p className="text-muted">
              Showing {startIndex + 1} to{" "}
              {Math.min(endIndex, filteredData.length)} of {filteredData.length}{" "}
              entries
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

BTable.propTypes = {
  tableName: PropTypes.string.isRequired,
  buttonAddTitle: PropTypes.string.isRequired,
  data: PropTypes.array.isRequired,
  columnOrder: PropTypes.array.isRequired,
  handleRoute: PropTypes.string.isRequired,
  handleDelete: PropTypes.func.isRequired,
};

export default BTable;
