import { Link, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import BCard from "../../components/layouts/BCard";
import BInputField from "../../components/layouts/BInputField";
import BInputLabel from "../../components/layouts/BInputLabel";
import BButton from "../../components/layouts/BButton";
import { Store } from "../users/actions/Actions";

const CreateUser = () => {
  const {
    register,
    handleSubmit,
    setError,
    formState: { errors },
  } = useForm();

  const navigate = useNavigate();

  const onSubmit = (data) => {
    Store(data, setError, navigate);
  };
  
  return (
    <BCard cardTitle="Create User">
      <form onSubmit={handleSubmit(onSubmit)}>
        {/* USERNAME */}
        <BInputLabel htmlFor="username" label="Username" />
        <BInputField
          type="text"
          id="username"
          name="username"
          placeholder="Username"
          register={register("username", {
            required: "Usename is required.",
          })}
          error={errors.username}
        />

        {/* EMAIL */}
        <BInputLabel htmlFor="email" label="Email" />
        <BInputField
          type="email"
          id="email"
          name="email"
          placeholder="Email"
          register={register("email", {
            required: "Email is required.",
          })}
          error={errors.email}
        />

        <div className="py-3">
          <BButton type="submit" buttonText="Add" />
          <Link to="/users" className="btn btn-danger shadow-none">
            Cancel
          </Link>
        </div>
      </form>
    </BCard>
  );
};

export default CreateUser;
