import React, { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import { useQuery } from "react-query";
import { GetProductById } from "./actions/Actions";
import { GetCategoryById } from "../categories/actions/Actions";
import BCard from "../../components/layouts/BCard";
import { BSkeleton } from "../../components/layouts/BSkeleton";

const ShowProduct = () => {
  const { id } = useParams();
  const [isLoading, setIsLoading] = useState(true);

  const { data: product, isLoading: isProductLoading } = useQuery(
    ["product", id],
    () => GetProductById(id),
    {
      staleTime: 0, // Disable caching for product retrieval
    }
  );

  const { data: category, isLoading: isCategoryLoading } = useQuery(
    ["category", product?.category],
    () => GetCategoryById(product?.category),
    {
      staleTime: 0, // Disable caching for category retrieval
      enabled: !!product?.category, // Only enable the query if the category ID exists
    }
  );

  useEffect(() => {
    if (!isProductLoading && !isCategoryLoading && isLoading) {
      setIsLoading(false);
    }
  }, [isProductLoading, isCategoryLoading, isLoading]);

  return (
    <BCard cardTitle={isLoading ? <BSkeleton /> : product && product.name}>
      {!isLoading ? (
        <>
          <p>
            <span className="h6 fw-bold">Category:</span>{" "}
            {category ? category.name : ""}
          </p>
          <p>
            <span className="h6 fw-bold">Description:</span>{" "}
            {product && product.description}
          </p>
          <p>
            <span className="h6 fw-bold">Price:</span>{" "}
            {product && product.price}
          </p>
          <p>
            <span className="h6 fw-bold">Created At:</span>{" "}
            {product && product.created_at}
          </p>
          <p>
            <span className="h6 fw-bold">Updated At:</span>{" "}
            {product && product.updated_at}
          </p>
          <div className="py-1">
            <Link to="/products" className="btn btn-danger shadow-none">
              Back
            </Link>
          </div>
        </>
      ) : (
        <BSkeleton />
      )}
    </BCard>
  );
};

export default ShowProduct;
