import React, { useState } from "react";
import { NavLink as Link, useNavigate } from "react-router-dom";
import logo from "../../assets/images/tranquilo-logo.svg";
import ScrollToTop from "../others/ScrollToTop";

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const accessToken = sessionStorage.getItem("accessToken");
  const navigate = useNavigate();

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleLogout = () => {
    sessionStorage.removeItem("accessToken");
    setIsMenuOpen(false);
    navigate("/", { replace: true });
  };

  const closeMenu = () => {
    setIsMenuOpen(false);
  };

  return (
    <>
      <ScrollToTop />
      <header className="sticky-header">
        <div className="nav-wrapper">
          <div className="logo-container">
            <Link to="/">
              <img className="logo" src={logo} alt="Logo" />
            </Link>
          </div>
          <nav>
            <input
              className="hidden"
              type="checkbox"
              id="menuToggle"
              checked={isMenuOpen}
              onChange={toggleMenu}
            />
            <label className="menu-btn" htmlFor="menuToggle">
              {isMenuOpen ? (
                <i className="fa fa-times" aria-hidden="true"></i>
              ) : (
                <i className="fa fa-bars" aria-hidden="true"></i>
              )}
            </label>
            <div className={`nav-container ${isMenuOpen ? "open" : ""}`}>
              <ul className="nav-tabs">
                <li>
                  <Link
                    className="nav-tab fw-bold"
                    to="/"
                    onClick={closeMenu} // Close menu when link is clicked
                  >
                    Menu
                  </Link>
                </li>
                <li>
                  <Link
                    className="nav-tab fw-bold"
                    to="/about"
                    onClick={closeMenu} // Close menu when link is clicked
                  >
                    About
                  </Link>
                </li>
                <li>
                  <Link
                    className="nav-tab fw-bold"
                    to="/contact"
                    onClick={closeMenu} // Close menu when link is clicked
                  >
                    Contact
                  </Link>
                </li>

                {accessToken && (
                  <>
                    <li>
                      <Link
                        className="nav-tab fw-bold"
                        to="/dashboard"
                        onClick={closeMenu} // Close menu when link is clicked
                      >
                        Dashboard
                      </Link>
                    </li>
                    <li>
                      <Link
                        className="nav-tab fw-bold"
                        to="/users"
                        onClick={closeMenu} // Close menu when link is clicked
                      >
                        Users
                      </Link>
                    </li>

                    <li>
                      <Link
                        className="nav-tab fw-bold"
                        to="/products"
                        onClick={closeMenu} // Close menu when link is clicked
                      >
                        Products
                      </Link>
                    </li>
                    <li>
                      <Link
                        className="nav-tab fw-bold"
                        to="/categories"
                        onClick={closeMenu} // Close menu when link is clicked
                      >
                        Categories
                      </Link>
                    </li>
                    <li>
                      <Link
                        to="/"
                        className="nav-tab fw-bold text-danger"
                        onClick={handleLogout}>
                        <span className="logout">
                          <i className="fa fa-sign-out" aria-hidden="true"></i>{" "}
                          Logout
                        </span>
                      </Link>
                    </li>
                  </>
                )}
              </ul>
            </div>
          </nav>
        </div>
      </header>
    </>
  );
};

export default Header;
