import React, { useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import { GetUserById } from "./actions/Actions";
import { useQuery } from "react-query";
import BCard from "../../components/layouts/BCard";
import { BSkeleton } from "../../components/layouts/BSkeleton";

const ShowUser = () => {
  const { id } = useParams();

  const { data: user, isLoading } = useQuery(["category", id], () => GetUserById(id),);

  useEffect(() => {
    const fetchData = async () => {
      await GetUserById(id);
    };
    fetchData();
  }, [id]);

  return (
    <BCard cardTitle={user ? user.username : <BSkeleton />}>
      {isLoading ? (
        <BSkeleton />
      ) : user ? (
        <>
          <p>
            <span className="h6 fw-bold">Email:</span> {user.email}
          </p>
          <p>
            <span className="h6 fw-bold">Created At:</span> {user.created_at}
          </p>
          <p>
            <span className="h6 fw-bold">Updated At:</span> {user.updated_at}
          </p>

          <div className="py-1">
            <Link to="/users" className="btn btn-danger shadow-none">
              Back
            </Link>
          </div>
        </>
      ) : (
        <BSkeleton />
      )}
    </BCard>
  );
};

export default ShowUser;
