import React from "react";
import { Link } from "react-router-dom";

const PageTitle = ({ srcImage, pageTitle, routePath, linkTitle }) => {
  return (
    <>
      <section className="section-title">
        <img src={srcImage} alt="" width="100%" />

        {routePath ? (
          <div className="page-title">
            <h2 className="page-title fw-bold">{pageTitle}</h2>
            <br></br>
            <div className="pt-5">
            <Link
              to={routePath}
              className="page-link btn btn-second pe-4 border-0 ps-4 py-2">
              {linkTitle}
            </Link>
            </div>
          </div>
        ) : (
          <h1 className="page-title fw-bold">{pageTitle}</h1>
        )}
      </section>
    </>
  );
};

export default PageTitle;
