import React, { useEffect, useState, useMemo } from "react";
import BMenuCard from "../components/layouts/BMenuCard";
import axios from "axios";
import { useQuery } from "react-query";

const Home = () => {
  const [existingProducts, setExistingProducts] = useState([]);
  const API_URL = process.env.REACT_APP_API_URL;
  const { data: productsData, isLoading: isProductsLoading } = useQuery(
    "productsData",
    async () => {
      const url = `${API_URL}/products.json`;
      const response = await axios.get(url);
      return response.data;
    }
  );

  const { data: categoriesData, isLoading: isCategoriesLoading } = useQuery(
    "categoriesData",
    async () => {
      const categoriesUrl = `${API_URL}/categories.json`;
      const response = await axios.get(categoriesUrl);
      return response.data;
    }
  );

  useEffect(() => {
    if (!isProductsLoading && !isCategoriesLoading) {
      const products = [];
      const categories = [];

      for (const [id, product] of Object.entries(productsData || {})) {
        products.push({ id, ...product });
      }

      for (const [id, category] of Object.entries(categoriesData || {})) {
        categories.push({ id, name: category.name });
      }

      const productsWithCategories = products.map((product) => {
        const category = categories.find((cat) => cat.id === product.category);
        product.category = category ? category.name : "";
        return product;
      });

      setExistingProducts(productsWithCategories);
    }
  }, [productsData, categoriesData, isProductsLoading, isCategoriesLoading]);

  const productsByCategory = useMemo(() => {
    const groupedProducts = {};
    existingProducts.forEach((product) => {
      if (!groupedProducts[product.category]) {
        groupedProducts[product.category] = [];
      }
      groupedProducts[product.category].push(product);
    });
    return groupedProducts;
  }, [existingProducts]);

  return (
    <div className="menu">
      <div className="container py-5 text-center">
        <div className="row">
          <div className="col-12 pb-4">
            <h1 className="fw-bold menu-title">
              <i className="fa fa-coffee menu-coffee" aria-hidden="true"></i> &nbsp;Menu
            </h1>
          </div>
        </div>

        {Object.entries(productsByCategory).map(([category, products]) => (
          <React.Fragment key={category}>
            <BMenuCard cardTitle={category} productArray={products} />
            {products.length === 0 && (
              <div className="row">
                <div className="col-12">
                  <div className="card text-center">
                    <div className="card-body">
                      <h5 className="card-title">{category}</h5>
                      <p className="card-text">Coming Soon</p>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </React.Fragment>
        ))}
      </div>
    </div>
  );
};

export default Home;
