import React from "react";
import BModal from "../../components/layouts/BModal";
import { Destroy, GetAllUsers } from "./actions/Actions";
import { useQueryClient } from "react-query";

const DeleteUser = ({ selectedUser, closeDeleteModal }) => {
  const queryClient = useQueryClient();

  const fetchData = async () => {
    try {
      await GetAllUsers();
      queryClient.invalidateQueries("users");
    } catch (error) {
      console.error(error);
    }
  };

  const Delete = async () => {
    const id = selectedUser.id;
    try {
      await Destroy(id, selectedUser);
      fetchData();
      closeDeleteModal();
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <BModal
      title="Confirm Deletion"
      message="Are you sure you want to delete this user?"
      confirmText="Delete"
      cancelText="Cancel"
      onConfirm={Delete}
      onCancel={closeDeleteModal}
    />
  );
};

export default DeleteUser;
