import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import BCardForm from "../../components/layouts/BCard";
import { GetAllUsers } from "../users/actions/Actions";
import BButton from "../../components/layouts/BButton";
import BInputField from "../../components/layouts/BInputField";

const Login = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const [existingUsers, setExistingUsers] = useState({});
  const navigate = useNavigate();
  // const [showPassword, setShowPassword] = useState(false);

  const fetchExistingUsers = async () => {
    try {
      const users = await GetAllUsers();
      if (users.length > 0) {
        const data = {};
        users.forEach((user) => {
          data[user.id] = user;
        });
        setExistingUsers(data);
      } else {
        throw new Error("Error fetching existing users");
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleLogin = (data) => {
    const { username, password } = data;

    if (existingUsers && Object.keys(existingUsers).length > 0) {
      const user = Object.values(existingUsers).find(
        (user) =>
          user.username &&
          user.username.toLowerCase() === username.toLowerCase() &&
          user.password === password
      );
      if (user) {
        sessionStorage.setItem("accessToken", user.access_token);
        navigate("/dashboard");
      } else {
        toast.error("Invalid Username or Password.");
      }
    } else {
      toast.error("Invalid username and password.");
    }
  };

  useEffect(() => {
    fetchExistingUsers();
  }, []);

  return (
    <>
      <BCardForm cardTitle="Login">
        <form onSubmit={handleSubmit(handleLogin)}>
          {/* USERNAME */}
          <BInputField
            type="text"
            id="username"
            name="username"
            placeholder="Username"
            register={register("username", {
              required: "Usename is required.",
            })}
            error={errors.username}
          />

          {/* PASSWORD */}
          <BInputField
            type="password"
            id="password"
            name="password"
            className="password form-control"
            placeholder="Password"
            autoComplete="current-passsword"
            register={register("password", {
              required: "Password is required.",
            })}
            error={errors.password}
          />

          <div className="py-2">
            <BButton type="submit" buttonText="Login" />
          </div>
        </form>
      </BCardForm>
    </>
  );
};

export default Login;
