import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useParams, useNavigate } from "react-router-dom";
import BCard from "../../components/layouts/BCard";
import BInputField from "../../components/layouts/BInputField";
import BInputLabel from "../../components/layouts/BInputLabel";
import BButton from "../../components/layouts/BButton";
import { useQuery } from "react-query";
import { Update, GetCategoryById } from "./actions/Actions";
import { BSkeleton } from "../../components/layouts/BSkeleton";

const EditCategory = () => {
  const {
    register,
    handleSubmit,
    setError,
    formState: { errors },
    setValue,
  } = useForm();

  const { id } = useParams();
  const navigate = useNavigate();

  const { data: categoryData, isLoading: isCategoryLoading } = useQuery(["category", id],() => GetCategoryById(id));

  const onSubmit = (data) => {
    Update(id, data, setError, navigate);
  };

  useEffect(() => {
    if (!isCategoryLoading && categoryData) {
      setValue("uuid", categoryData.uuid || "");
      setValue("name", categoryData.name || "");
      setValue("created_at", categoryData.created_at || "");
      setValue("updated_at",new Date().toDateString() + " At " + new Date().toLocaleTimeString());
    }
  }, [categoryData, isCategoryLoading, setValue]);

  return (
    <BCard cardTitle="Edit Category">
      {isCategoryLoading ? (
        <BSkeleton />
      ) : (
        <form onSubmit={handleSubmit(onSubmit)}>
          {/* CATEGORY NAME */}
          <BInputLabel htmlFor="name" label="Category Name" />
          <BInputField
            type="text"
            id="name"
            name="name"
            placeholder="Category Name"
            register={register("name", {
              required: "Category name is required.",
            })}
            error={errors.name}
          />
          <div className="py-3">
            <BButton type="submit" buttonText="Update" />
            <Link to="/categories" className="btn btn-danger shadow-none">
              Cancel
            </Link>
          </div>
        </form>
      )}
    </BCard>
  );
};

export default EditCategory;
