import { v4 as uuidv4 } from "uuid";
import { toast } from "react-toastify";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL;

export const Store = async (data, setError, navigate) => {
  const { name, category } = data;
  const product = {
    uuid: uuidv4(),
    category,
    created_at:
      new Date().toDateString() + " At " + new Date().toLocaleTimeString(),
    updated_at:
      new Date().toDateString() + " At " + new Date().toLocaleTimeString(),
    ...data,
  };
  const url = `${API_URL}/products.json`;
  const lowercaseName = name.toLowerCase();

  try {
    const response = await axios.get(url);
    const existingProducts = response.data || {};

    if (
      Object.values(existingProducts).some(
        (product) => product.name.toLowerCase() === lowercaseName
      )
    ) {
      setError("name", {
        type: "manual",
        message: "Product already exists",
      });
      return;
    }

    const addProductResponse = await axios.post(url, product, {
      headers: {
        "Content-Type": "application/json",
      },
    });

    if (!addProductResponse.data) {
      throw new Error("Error creating product.");
    }

    navigate("/products");
    toast.success("Product Created Successfully!");
  } catch (error) {
    console.error(error);
  }
};

export const Update = async (id, data, setError, categories, navigate) => {
  const { uuid, name, description, price, category, created_at, updated_at } =
    data;
  const updatedProduct = {
    uuid,
    name,
    description,
    price,
    category,
    created_at,
    updated_at,
  };
  const url = `${API_URL}/products/${id}.json`;
  const lowercaseName = name.toLowerCase();

  try {
    const response = await axios.get(`${API_URL}/products.json`);
    const existingProducts = response.data || {};

    const duplicateProduct = Object.values(existingProducts).find(
      (product) =>
        product.uuid !== uuid && product.name.toLowerCase() === lowercaseName
    );

    if (duplicateProduct) {
      setError("name", {
        type: "manual",
        message: "Product already exists",
      });
      return;
    }

    const categoryId = categories.find((cat) => cat.name === category)?.id;

    if (!categoryId) {
      setError("category", {
        type: "manual",
        message: "Invalid category selected",
      });
      return;
    }

    updatedProduct.category = categoryId;

    const updateProductResponse = await axios.put(url, updatedProduct, {
      headers: {
        "Content-Type": "application/json",
      },
    });

    if (updateProductResponse.status !== 200) {
      throw new Error("Error updating product.");
    }

    navigate("/products");
    toast.success("Product Updated Successfully.");
  } catch (error) {
    console.error(error);
    throw new Error("Error updating product. Please try again later.");
  }
};

export const Destroy = async (id) => {
  const url = `${API_URL}/products/${id}.json`;
  try {
    const deletePromise = axios.delete(url, {
      method: "DELETE",
    });
    toast.success("Product Deleted Successfully.");
    await deletePromise;
  } catch (error) {
    console.error(error);
  }
};

export const GetAllProducts = async () => {
  try {
    const productsUrl = `${API_URL}/products.json`;
    const categoriesUrl = `${API_URL}/categories.json`;

    const productsResponse = await axios.get(productsUrl);
    const productsData = productsResponse.data;
    const products = Object.entries(productsData || {}).map(
      ([id, product]) => ({
        id,
        ...product,
      })
    );

    const categoriesResponse = await axios.get(categoriesUrl);
    const categoriesData = categoriesResponse.data;
    const fetchedCategories = Object.entries(categoriesData || {}).map(
      ([id, category]) => ({
        id,
        ...category,
      })
    );

    return { products, categories: fetchedCategories };
  } catch (error) {
    console.error(error);
    return { products: [], categories: [] };
  }
};

export const GetProductById = async (id) => {
  try {
    const url = `${API_URL}/products/${id}.json`;
    const response = await axios.get(url);

    if (!response.status === 200) {
      throw new Error("Error Fetching Product.");
    }

    return response.data || {};
  } catch (error) {
    console.error(error);
    return null;
  }
};

export const CountProducts = async () => {
  const url = `${API_URL}/products.json`;

  try {
    const response = await axios.get(url);
    const data = response.data;
    const count = Object.keys(data || {}).length;

    return count;
  } catch (error) {
    console.error(error);
    return 0;
  }
};
