import { v4 as uuidv4 } from "uuid";
import { toast } from "react-toastify";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL;

export const Store = async (data, setError, navigate) => {
  try {
    const { name } = data;
    const category = {
      uuid: uuidv4(),
      created_at: new Date().toLocaleString(), // Format the date using toLocaleString
      updated_at: new Date().toLocaleString(), // Format the date using toLocaleString
      ...data,
    };

    const lowercaseName = name.toLowerCase();

    const response = await axios.get(`${API_URL}/categories.json`);
    const existingCategories = response.data || {};

    if (
      Object.values(existingCategories).some(
        (category) => category.name.toLowerCase() === lowercaseName
      )
    ) {
      setError("name", {
        type: "manual",
        message: "Category already exists",
      });
      return;
    }

    const addCategoryResponse = await axios.post(
      `${API_URL}/categories.json`,
      category,
      {
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (!addCategoryResponse.data) {
      throw new Error("Error creating category.");
    }

    toast.success("Category Created Successfully.");
    navigate("/categories");
  } catch (error) {
    console.error(error);
    // Handle error and show error toast message if needed
  }
};

export const Update = async (id, data, setError, navigate) => {
  try {
    const { uuid, name, created_at, updated_at } = data;
    const updatedCategory = { uuid, name, created_at, updated_at };

    const response = await axios.get(`${API_URL}/categories.json`);
    const existingCategories = response.data || {};

    const lowercaseName = name.toLowerCase();

    if (
      Object.values(existingCategories)
        .filter((category) => category.uuid !== uuid)
        .some((category) => category.name.toLowerCase() === lowercaseName)
    ) {
      setError("name", {
        type: "manual",
        message: "Category already exists",
      });
      return;
    }

    const updateCategoryResponse = await axios.put(
      `${API_URL}/categories/${id}.json`,
      updatedCategory,
      {
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (updateCategoryResponse.status !== 200) {
      throw new Error("Error updating category.");
    }

    toast.success("Category Updated Successfully.");
    navigate("/categories");
  } catch (error) {
    console.error(error);
    // Handle error and show error toast message if needed
  }
};

export const Destroy = async (id) => {
  try {
    await axios.delete(`${API_URL}/categories/${id}.json`);

    const response = await axios.get(`${API_URL}/products.json`);
    const products = response.data || {};

    const updatedProducts = Object.entries(products).reduce(
      (acc, [productId, product]) => {
        if (product.category === id) {
          return acc;
        }
        return { ...acc, [productId]: product };
      },
      {}
    );

    await axios.put(`${API_URL}/products.json`, updatedProducts);

    toast.success("Category Deleted Successfully.");
  } catch (error) {
    console.error(error);
    // Handle error and show error toast message if needed
  }
};

export const GetAllCategories = async () => {
  try {
    const response = await axios.get(`${API_URL}/categories.json`);
    const data = response.data;
    const categories = Object.entries(data || {}).map(([id, category]) => ({
      id,
      ...category,
    }));

    return categories;
  } catch (error) {
    console.error(error);
    // Handle error and show error toast message if needed
    return [];
  }
};

export const GetCategoryById = async (id) => {
  try {
    const response = await axios.get(`${API_URL}/categories/${id}.json`);

    if (response.status !== 200) {
      throw new Error("Error Fetching Product.");
    }

    return response.data || {};
  } catch (error) {
    console.error(error);
    // Handle error and show error toast message if needed
    return null;
  }
};

export const CountCategories = async () => {
  try {
    const response = await axios.get(`${API_URL}/categories.json`);
    const data = response.data;
    const count = Object.keys(data || {}).length;

    return count;
  } catch (error) {
    console.error(error);
    // Handle error and show error toast message if needed
    return 0;
  }
};
