import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useParams, useNavigate } from "react-router-dom";
import BCard from "../../components/layouts/BCard";
import BInputField from "../../components/layouts/BInputField";
import BInputLabel from "../../components/layouts/BInputLabel";
import BButton from "../../components/layouts/BButton";
import { GetAllCategories } from "../categories/actions/Actions";
import { Update, GetProductById } from "./actions/Actions";
import { useQuery } from "react-query";
import { BSkeleton } from "../../components/layouts/BSkeleton";

const EditProduct = () => {
  const {
    register,
    handleSubmit,
    setError,
    formState: { errors },
    setValue,
  } = useForm();

  const { id } = useParams();
  const navigate = useNavigate();
  const [product, setProduct] = useState(null);
  const [categories, setCategories] = useState([]);

  const { data: productData, isLoading: isProductLoading } = useQuery(
    ["product", id],
    () => GetProductById(id)
  );

  const { data: categoriesData, isLoading: isCategoryLoading } = useQuery(
    "categories",
    GetAllCategories
  );

  const updateProduct = async (data) => {
    try {
      await Update(id, data, setError, categoriesData, navigate);
    } catch (error) {
      console.error(error);
      throw new Error("Error updating product. Please try again later.");
    }
  };

  useEffect(() => {
    if (categoriesData) {
      setCategories(categoriesData);
    }
  }, [categoriesData]);

  useEffect(() => {
    if (productData) {
      setProduct(productData || {});
    }
  }, [productData]);

  useEffect(() => {
    if (!isCategoryLoading && !isProductLoading && product) {
      setValue("uuid", product?.uuid || "");
      setValue("name", product?.name || "");
      setValue("description", product?.description || "");
      setValue("price", product?.price || "");
      setValue("created_at", product?.created_at || "");
      setValue(
        "updated_at",
        new Date().toDateString() + " At " + new Date().toLocaleTimeString()
      );

      const selectedCategory = categories.find(
        (category) => category.id === product?.category
      );
      setValue("category", selectedCategory?.name || "");
    }
  }, [product, categories, setValue, isCategoryLoading, isProductLoading]);

  return (
    <BCard cardTitle="Edit Product">
      {isProductLoading ? (
        <BSkeleton />
      ) : (
        <form onSubmit={handleSubmit(updateProduct)}>
          {/* PRODUCT NAME */}
          <BInputLabel htmlFor="name" label="Product Name" />
          <BInputField
            type="text"
            id="name"
            name="name"
            placeholder="Product Name"
            register={register("name", {
              required: "Product name is required.",
            })}
            error={errors.name}
          />

          {/* PRODUCT DESCRIPTION */}
          <BInputLabel htmlFor="description" label="Product Description" />
          <BInputField
            type="text"
            id="description"
            name="description"
            placeholder="Product Description"
            defaultValue="N/A"
            register={register("description", {
              required: "Product description is required.",
            })}
            error={errors.description}
          />

          {/* PRODUCT CATEGORY */}
          <div className="py-1">
            <label className="fw-bold pb-1">Product Category</label>
            <select
              id="category"
              className="form-control"
              name="category"
              {...register("category", {
                required: "Product category is required.",
              })}>
              <option value="" className="text-muted">
                -- Select Category --
              </option>
              {categories.map((category) => (
                <option key={category.id} value={category.name}>
                  {category.name}
                </option>
              ))}
            </select>

            {errors.category && (
              <small className="text-danger fw-bold">
                {errors.category.message}
              </small>
            )}
          </div>

          {/* PRODUCT PRICE */}
          <BInputLabel htmlFor="price" label="Product Price" />
          <BInputField
            type="text"
            id="price"
            name="price"
            placeholder="Product Price"
            register={register("price", {
              required: "Product price is required.",
            })}
            error={errors.price}
          />

          <div className="py-3">
            <BButton type="submit" buttonText="Update" />
            <Link to="/products" className="btn btn-danger shadow-none">
              Cancel
            </Link>
          </div>
        </form>
      )}
    </BCard>
  );
};

export default EditProduct;
