import React from "react";
import BModal from "../../components/layouts/BModal";
import { Destroy, GetAllProducts } from "./actions/Actions";
import { useQueryClient } from "react-query";


const DeleteProduct = ({ selectedProduct, closeDeleteModal }) => {
  const queryClient = useQueryClient();
  
  const fetchData = async () => {
    try {
      await GetAllProducts();
      queryClient.invalidateQueries("products");
    } catch (error) {
      console.error(error);
    }
  };

  const Delete = async () => {
    const id = selectedProduct.id;
    try {
      await Destroy(id, selectedProduct);
      fetchData();
      closeDeleteModal();
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <BModal
      title="Confirm Deletion"
      message="Are you sure you want to delete this product?"
      confirmText="Delete"
      cancelText="Cancel"
      onConfirm={Delete}
      onCancel={closeDeleteModal}
    />
  );
};

export default DeleteProduct;
