import React, { useEffect, useState } from "react";
import { useQuery, useQueryClient } from "react-query";
import BTable from "../../components/layouts/BTable";
import DeleteCategory from "./DeleteCategory";
import { GetAllCategories } from "./actions/Actions";

const GetCategories = () => {

  const { data: existingCategories = [] } = useQuery("categories", GetAllCategories, { staleTime: 0 });

  const [selectedCategory, setSelectedCategory] = useState(null);
  const queryClient = useQueryClient();

  useEffect(() => {
    const fetchData = async () => {
      try {
        await GetAllCategories();
        queryClient.invalidateQueries("categories");
      } catch (error) {
        console.error(error);
      }
    };
    fetchData();
  }, [queryClient]);

  const handleDelete = (category) => {
    setSelectedCategory(category);
  };

  const closeDeleteModal = () => {
    setSelectedCategory(null);
  };

  return (
    <>
      <BTable
        tableName="Categories"
        buttonAddTitle="Add Category"
        data={existingCategories}
        columnOrder={["name"]}
        handleDelete={handleDelete}
        handleRoute="categories"
      />

      {/* DELETE MODAL */}
      {selectedCategory && (
        <DeleteCategory
          selectedCategory={selectedCategory}
          closeDeleteModal={closeDeleteModal}
        />
      )}
    </>
  );
};

export default GetCategories;
