import React, { useState, useEffect } from "react";
import BPageTitle from "../../components/layouts/BPageTitle";
import welcome from "../../assets/images/dashboard1.svg";
import { useQuery } from "react-query";
import { CountCategories } from "../../features/categories/actions/Actions";
import { CountProducts } from "../../features/products/actions/Actions";
import {
  CountUsers,
  getUserAuthName,
} from "../../features/users/actions/Actions"; // Import getUserAuthName
import { BSkeleton } from "../../components/layouts/BSkeleton";

const Dashboard = () => {
  const { data: categoryCount, isLoading: isCategoryLoading } = useQuery(
    "categoryCount",
    CountCategories
  );
  const { data: productCount, isLoading: isProductLoading } = useQuery(
    "productCount",
    CountProducts
  );
  const { data: userCount, isLoading: isUserLoading } = useQuery(
    "userCount",
    CountUsers
  );
  const { data: userName } = useQuery("authUserName", () =>
    getUserAuthName(sessionStorage.getItem("accessToken"))
  );

  const [isLoading, setIsLoading] = useState(true);

  if (!isCategoryLoading && !isProductLoading && !isUserLoading && isLoading) {
    setIsLoading(false);
  }

  const cardData = [
    {
      title: "Total Users",
      count: userCount,
    },
    {
      title: "Total Categories",
      count: categoryCount,
    },
    {
      title: "Total Products",
      count: productCount,
    },
  ];

  useEffect(() => {
    const fetchAuthName = async () => {
      try {
        const accessToken = sessionStorage.getItem("accessToken");
        await getUserAuthName(accessToken);
      } catch (error) {
        console.error(error);
      }
    };
    fetchAuthName();
  }, []);

  return (
    <>
      <BPageTitle
        srcImage={welcome}
        pageTitle={`Welcome ${userName ? userName : ""} `} // Display the authenticated user's name
        routePath="/qrcode"
        linkTitle="Menu QR Code"
      />

      <div className="container py-5">
        <div className="row">
          {cardData.map((card, index) => (
            <div key={index} className="col-lg-4 col-md-6 col-sm-12 py-1">
              <div className="card text-center">
                <div className="card-header pb-0">
                  <h4 className="fw-bold">{card.title}</h4>
                </div>
                <div className="card-body">
                  {isLoading ? (
                    <BSkeleton />
                  ) : typeof card.count === "number" ? (
                    <h2>{card.count}</h2>
                  ) : (
                    <small>{card.count}</small>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Dashboard;